.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f0f0f0;
  min-height: 100vh;
}

.header {
  width: 100%;
  background-color: #007bff;
  color: white;
  padding: 20px;
  text-align: center;
  margin-bottom: 20px;
  border-radius: 5px;
}

.content {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.product-list {
  width: 70%;
}

.cart {
  width: 25%;
}
